import car_img from "./car.png"
import car_img2 from "./EVO.png"
import { useState } from "react";

function Car(props) {

    const [current_car, setCurrentCar] = useState("")
    if (props.car != current_car) {
        setCurrentCar(props.car)
    }
        return (

            <div className="car-container">
                <img key={current_car} src={car_img} alt={current_car}/>
            </div>
        )
    }
    
    export default Car;
    